.postsSection {
  display: flex;
  flex-direction: column;
  width: 600px;
  gap: 20px;
}

/* ê²Œì‹œë¬¼ ì»¨í…Œì´ë„ˆ */
.postContainer {
  background: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  width: 100%;
  border: 1px solid #ddd;
  margin-bottom: 20px;
}

.postContent {
  margin: 10px 0;
  font-size: 16px;
}

/* ê²Œì‹œë¬¼ í—¤ë” */
.postHeader {
  display: flex;
    justify-content: space-between;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 30px;
}

/* ì´ë¯¸ì§€ ìŠ¬ë¼ì´ë” */
.imageSlider {
  position: relative;
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 5px;
}

/* ì´ë¯¸ì§€ */
.postImage {
  width: 500px;
  height: 500px;
  object-fit: contain;
  border-radius: 5px;
}

/* ìŠ¬ë¼ì´ë” ì»¨íŠ¸ë¡¤ (í™”ì‚´í‘œ) */
.sliderControls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
  width: 100%;
  pointer-events: none;
}

.prevButton,
.nextButton {
  display: flex !important;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 50%;
  pointer-events: auto;
  transition: background-color 0.2s;
}

.prevButton:hover,
.nextButton:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.prevButton {
  left: 5px;
}

.nextButton {
  right: 5px;
}

