.postFeed {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

/* ì™¼ìª½ ì‚¬ì´ë“œë°” */
.sidebar {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: fixed;
  left: 20px;
  top: 100px;
}

.navLink{
  text-align: center;
  background: none;
  border: none;
  text-decoration: none;
  font-size: 16px;
  color: black;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s, color 0.2s;
}

.navLink:hover{
  background: #f0f0f0;
  color: #007bff;
}

/* ê²Œì‹œë¬¼ ì»¨í…Œì´ë„ˆ */
.postsContainer {
  display: flex;
  flex-direction: column;
  width: 600px;
  gap: 20px;
}

/* ê²Œì‹œë¬¼ ë³¸ë¬¸ */
.postContainer {
  background: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  width: 100%;
  border: 1px solid #ddd;
  margin-bottom: 20px;
}

/* ê²Œì‹œë¬¼ í—¤ë” (ì´ë¦„ê³¼ ë‚ ì§œ) */
.postHeader {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 30px;
}

/* ê²Œì‹œë¬¼ ë³¸ë¬¸ */
.postContent {
  margin: 10px 0;
  font-size: 16px;
}

/* ì´ë¯¸ì§€ ìŠ¬ë¼ì´ë” */
.imageSlider {
  position: relative;
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 5px;
}

.postImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 5px;
}

/* ìŠ¬ë¼ì´ë” ì»¨íŠ¸ë¡¤ */
.sliderControls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
  width: 100%;
  pointer-events: none;
}

.prevButton, .nextButton {
  display: flex !important;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 50%;
  pointer-events: auto;
  transition: background-color 0.2s;
}

.prevButton:hover, .nextButton:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.prevButton {
  left: 5px;
}

.nextButton {
  right: 5px;
}

/* ê²Œì‹œë¬¼ í‘¸í„° */
.postFooter {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 3px;
}

/* ì¢‹ì•„ìš” ë²„íŠ¼ */
.postLikeButton {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 0;
  width: auto;
}

.postLikeButton:hover {
  opacity: 0.7;
  background: #f0f0f0;
}

/* ì¢‹ì•„ìš” ê°œìˆ˜ */
.postLikes {
  font-size: 16px;
}

/* íƒœê·¸ */
.postTags {
  margin-top: 10px;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.postTag {
  font-size: 14px;
  color: #555;
}

.postCommentButton {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 0;
  width: auto;
  transition: opacity 0.3s;
  margin-left: 15px;
}

.postCommentButton:hover {
  opacity: 0.7;
  background: #f0f0f0;
}

.postComments {
  font-size: 16px;
}
