/* Profile.css */
.profile-container {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  padding: 20px;
}

.sidebar {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: fixed;
  left: 20px;
  top: 100px;
}

