}

.post-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
  margin-top: 20px;
}

.post-item {
  width: 100%;
  padding-top: 100%;
  position: relative;
  background-color: #f0f0f0;
  cursor: pointer;
}

.post-thumbnail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

